<template>
 <div class="imageLoader-container">
     <img class="source-img" :src="src" alt="" @load="changeImg">
     <img class="place-img" v-if="!completed" :src="placeholder" alt="" :style="{opacity:changeOpacity,transition:`${duration}ms`}">
 </div>
</template>

<script>
export default {
    props:{
        src:{
            type:String,
            required:true
        },
        placeholder:{
            type:String,
            required:true
        },
        duration:{
            type:Number,
            required:false,
            default:500
        }
    },
    data(){
        return{
            sourceStatu:false,
            completed:false
        }
    },
    computed:{
        changeOpacity(){
          return this.sourceStatu ? 0 : 1;
        }
    },
    methods:{
        changeImg () {
             this.sourceStatu = true;
            
             setTimeout(()=>{
                  this.$emit('load')
                 this.completed = true
             },this.duration)
           
        }
    }
}
</script>

<style scoped lang='less'>
@import '~@/styles/mix.less';
.imageLoader-container{
    position: relative;
    width:100%;
    height: 100%;
}
.source-img{
    .self-fill();
    object-fit: cover;
}
.place-img{
.self-fill();
    object-fit: cover;
    filter: blur(2vw);
}

</style>