<template>
  <div class="carousel-item-container" ref="container" @mousemove="this.handleMouseMove" @mouseleave="this.handleMouseLeave">
      <div class="carousel-img" ref="image" :style="imgPosition">
        <ImageLoader :src='carousel.bigImg' :placeholder="carousel.midImg" @load="this.showWords"/>
      </div>
      <div class="title" ref="title">{{carousel.title}}</div>
      <div class="desc" ref="desc">{{carousel.description}}</div>
  </div>
</template>

<script>
import ImageLoader from '@/components/ImageLoader'
export default {
  components:{
    ImageLoader
  },
  props:["carousel"],
  data() {
    return{
      titleWidth:0,
      descWidth:0,
      innerSize:null,
      containerSize:null,
      mouseX:0,
      mouseY:0
    }
  },
  computed:{
    imgPosition(){
      if (!this.innerSize || !this.containerSize) {
        return;
      }
      const extraWidth = this.innerSize.width - this.containerSize.width;
     const extraHeight = this.innerSize.height - this.containerSize.height;
      const left = (-extraWidth / this.containerSize.width) * this.mouseX;
      const top = (-extraHeight / this.containerSize.height) * this.mouseY;
      return{
        transform: `translate(${left}px, ${top}px)`,
      }
    },
      center() {
      return{
          x: this.containerSize.width / 2,
          y: this.containerSize.height / 2,
      }
    },
  },

  mounted(){
     this.titleWidth = this.$refs.title.clientWidth;
     this.descWidth = this.$refs.desc.clientWidth;
     this.setSize();
    this.mouseX = this.center.x;
    this.mouseY = this.center.y;
    window.addEventListener("resize", this.setSize);
  },
   destroyed() {
    window.removeEventListener("resize", this.setSize);
  },
  methods:{
    showTitle () {
    this.$refs.title.style.opacity = 1;
    this.$refs.title.style.width = 0;
    this.$refs.title.clientWidth;
    this.$refs.title.style.transition = "1s";
    this.$refs.title.style.width = this.titleWidth + 'px'
    },
    showDesc () {
    this.$refs.desc.style.opacity = 1;
    this.$refs.desc.style.width = 0;
    this.$refs.desc.clientWidth;
    this.$refs.desc.style.transition = "1s 2s";
    this.$refs.desc.style.width = this.descWidth + 'px'
    },
    showWords() {
      this.showTitle();
      this.showDesc();
    },
    handleMouseMove(e) {
      let rect = this.$refs.container.getBoundingClientRect();
      this.mouseX = e.clientX - rect.left;
      this.mouseY = e.clientY - rect.top;

    },
        handleMouseLeave() {
      this.mouseX = this.center.x;
      this.mouseY = this.center.y;
    },
    setSize() {
       this.innerSize ={
       width:this.$refs.image.clientWidth,
       height:this.$refs.image.clientWidth
     }
     this.containerSize ={
       width:this.$refs.container.clientWidth,
       height:this.$refs.container.clientWidth
     }
    },
    
  }

}
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.carousel-item-container {
  width: 100%;
  height: 100%;
  color: #fff;
  position: relative;
    overflow: hidden;
}
.carousel-img {
  width: 110%;
  height: 110%;
  transition: 0.3s;
    position: absolute;
  left: 0;
  top: 0;
}
.title,
.desc {
  position: absolute;
  letter-spacing: 3px;
  left: 30px;
  color: #fff;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.5), -1px 0 0 rgba(0, 0, 0, 0.5),
    0 1px 0 rgba(0, 0, 0, 0.5), 0 -1px 0 rgba(0, 0, 0, 0.5);
  white-space: nowrap;
  overflow: hidden;
  opacity: 0;
}
.title {
  top: calc(50% - 40px);
  font-size: 2em;
}
.desc {
  top: calc(50% + 20px);
  font-size: 1.2em;
  color: lighten(@gray, 20%);
}
</style>