<template>
  <div class="home-container" ref="container">
   <ul class="carousel-container"  :style="{
     marginTop
   }" @wheel="handleWheel" @transitionend="handleEnd" v-loading="loading">
     <li v-for="(item ) in data" :key="item.id" >
       <CarouselItem :carousel = "item" />
     </li>
   </ul>
   <div class="icon icon-up" v-show="index > 0" @click="switchTo(index - 1)">
     <Icon type='arrowUp' />
   </div>
   <div class="icon icon-down" v-show="index < data.length - 1" @click="switchTo(index + 1)">
     <Icon type='arrowDown' />
   </div>
   <ul class="indicator">
     <li  v-for="(item ,i ) in data" :key="item.id" :class="{active:i === index}" @click="switchTo(i)"></li>
   </ul>
  </div>
</template>

<script>
import Icon from '@/components/icon'
import { mapState } from 'vuex'
import CarouselItem from './carouselitem.vue'
export default {
  components:{
    CarouselItem,
    Icon,
  },
  data() {
    return  {
        index:0,
        containerHeight:0,
        switching:false,

    }

  },
  methods:{
    switchTo(i) {
      this.index = i
    },
    handleWheel(e) {
      if(this.switching){
        return
      }
      if(e.deltaY < -5 && this.index > 0){
        this.switching = true;
        this.index --
      }
      if(e.deltaY > 5 && this.index < this.data.length-1){
        this.switching = true;
        this.index ++
      }
    },
    handleEnd() {
      this.switching = false;
    },
    handleResize() {
      this.containerHeight =  this.$refs.container.clientHeight;
    },
  },
  mounted() {
    this.containerHeight =  this.$refs.container.clientHeight;
    window.addEventListener('resize',this.handleResize)
  },
  computed:{
    marginTop() {
      return -this.index * this.containerHeight + 'px'
    },
    ...mapState("banner",["loading","data"])

  },
  destryoed() {
    window.removeEventListener('resize',this.handleResize)
  },
  created(){
    this.$store.dispatch("banner/fetchBanner")
  }
}
</script>

<style scoped lang='less'>
@import '~@/styles/mix.less';
@import '~@/styles/var.less';
.home-container{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: @dark;
    overflow: hidden;
}
.carousel-container{
   width: 100%;
  height: 100%;
  transition: 500ms;

  li{
     width: 100%;
  height: 100%;
  }
}
.icon{
  .self-center();
  font-size: 30px;
  color: @gray;
  cursor: pointer;
  transform: translateX(-50%);
  @gap:15px;
  &.icon-up{
    top: @gap;
    animation: jump-up 2s infinite; 
  }
  &.icon-down{
    bottom: @gap;
    top: auto;
    animation: jump-down 2s infinite;
  }
  // 跳跃动画
  @jump:5px;
  @keyframes jump-up {
    0%{
      transform: translateX(-50%) translateY(@jump);
    }
    50%{
      transform: translateX(-50%) translateY(-@jump);
    }
    100%{
      transform: translateX(-50%) translateY(@jump);
    }
  }
   @keyframes jump-down {
    0%{
      transform: translateX(-50%) translateY(-@jump);
    }
    50%{
      transform: translateX(-50%) translateY(@jump);
    }
    100%{
      transform: translateX(-50%) translateY(-@jump);
    }
  }
}
.indicator{
  .self-center();
  transform: translateY(-50%);
  left: auto;
  right: 20px;
   li {
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: @words;
    cursor: pointer;
    margin-bottom: 10px;
    border: 1px solid #fff;
    box-sizing: border-box;
    &.active {
      background: #fff;
    }
  }
}
</style>